


































.user-info__card {
  position: absolute;
  top: 1.5rem;
  left: 1rem;
  width: 5rem;
  height: 3rem;
  padding: 0.3rem 0.4rem;
  border-top: 1px solid #eee;
  border-radius: 7px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
}
.user-info__card img {
  float: left;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
}
.user-info__site {
  padding: 0.12rem;
  text-align: center;
}
.user-info__site a {
  color: #999;
}
.user-info__site a:hover {
  color: #7a7a7a;
}
.user-info__site a:active {
  color: #737373;
}
.user-info__site svg {
  fill: currentColor;
}
.user-info__desc {
  margin-top: 0;
  text-align: center;
  color: #ccc;
}
.user-info__list {
  padding: 0.2rem 0.1rem;
  color: #999;
}
.user-info__item {
  line-height: 2.5;
  list-style: none;
}
.user-info__item svg {
  width: 16px;
  height: 16px;
  margin-right: 0.05rem;
  vertical-align: text-bottom;
}
.user-info__item p {
  display: inline-block;
  margin: 0;
}



